import React, {useState} from 'react';

import {Button} from '../ButtonElement';
import { 
HeroContainer,
HeroBg,
 VideoBg,
 HeroContent,
 HeroH1,
 HeroP,
 HeroBtnWrapper,
 ArrowForward,
 ArrowRight
 } from './HeroElements';

const HeroSection = () => {
    const [hover, setHover] = useState(false);
    
    const onHover = () => {
        setHover(!hover)
    }
    return (
        <HeroContainer id='home'>
            <HeroBg>
                <VideoBg />
            </HeroBg>
            <HeroContent>
                <HeroH1>Portfolio Portaels</HeroH1>
                <HeroP>
                Front-end / Graphic design</HeroP>
                <HeroBtnWrapper>
                    <Button to='Projects'
                    onMouseEnter={onHover} 
                    onMouseLeave={onHover}
                    primary="true"
                    dark ="true"
                    smooth={true}
                        duration={500} 
                        spy={true} 
                        exact='true'
                        offset={-80}
                    >
                     Take a look {hover ? <ArrowForward /> : <ArrowRight/>}
                    </Button>
                </HeroBtnWrapper>           
            </HeroContent>
        </HeroContainer>
    );
};

export default HeroSection
